
.product-item {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px 10px 10px;
  margin-bottom: 5px;
  background: var(--tg-theme-bg-color);
}

.product-item__img {
  max-width: 120px;
}

.product-item__img img {
  max-width: 100%;
  height: auto;
}

.product-item__price, .product-item__description {
  color: var(--tg-theme-text-color);
}

.product-item__block {
  text-align: left;
  width: 80%;
  max-width: 400px;
  margin-left: 20px;
}

.product-item__actions {
  display: flex;
  justify-content: space-between;
}

.product-item__stepper {
  display: flex;
  align-items: center;
  border-radius: 3px;
  background: var(--tg-theme-button-color);
}

.product-item__button, .product-item__count {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  border: none;
  background: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
}

.product-item__button {
  width: 30px;
  height: 30px;
  padding: 5px;
}


.product-item__count {
  width: 40px;
  text-align: center;
}
.product-item__icon {
  width: 16px;
  height: 16px;
  fill: var(--tg-theme-button-text-color);
}

.product-item__remove {
  width: 30px;
  display: flex;
}

.product-item__icon--remove {
  stroke: var(--tg-theme-hint-color);
  width: 22px;
  height: auto;
}
