.categories {
  background: var(--tg-theme-bg-color);
  height: 100vh;
}

.categories-title {
  margin: 0;
  padding: 20px 0;
}

.categories-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.categories-link {
  text-decoration: none;
  color: var(--tg-theme-text-color);
}

.categories-name {
  margin: 0 0 10px;
  font-size: 20px;
}