
.cart-info {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin: 0 20px;
  color: var(--tg-theme-text-color);
}

.cart-info__title {
  font-size: 28px;
  margin: 0;
}

.cart-info__total {
  font-size: 22px;
  font-weight: 500;
}

.cart-product {
  margin-top: 20px;
}

.cart-product__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.cart-order {
  margin: 20px 20px 0;
}

.cart-order__create {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 46px;
  padding: 5px 20px;
  font-size: 16px;
  text-decoration: none;
  color: var(--tg-theme-button-text-color);
  background: var(--tg-theme-button-color);
  border-radius: 50px;
}