.products {
  padding-bottom: 20px;
}

.products-title {
  margin: 0;
  margin-bottom: 20px;
}

.products-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

.products-link {
  text-decoration: none;
  display: inherit;
  color: var(--tg-theme-text-color);
}

.products-card {
  display: inherit;
  padding: 10px 9px 13px;
  background: var(--tg-theme-bg-color);
  border-radius: 5px;
  cursor: pointer;
  height: 100%;
  box-sizing: border-box;
}

.products-block {
  display: block;
  width: 100%;
  position: relative;
  padding-top: 100%;
  margin-bottom: 10px;
}

.products-block__img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  object-fit: contain;
  object-position: center;
}

.products-description, .products-price {
  margin: 0;
  text-align: left;
}

.products-description {
  margin-bottom: 10px;
  color: var(--tg-theme-hint-color);
}

.products-price {
  margin-top: auto;
}